.starsList {
  text-align: center;
  padding: 20px;
  background: #1e1e2e;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  max-width: 400px;
  margin: 20px auto;
  color: #ffffff;
}

.title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
}

.buyButton, .spendButton {
  display: block;
  width: 100%;
  padding: 12px;
  margin-top: 10px;
  font-size: 16px;
  font-weight: bold;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: 0.3s ease;
}

.buyButton {
  background-color: #ffcc00;
  color: #1e1e2e;
}

.buyButton:hover {
  background-color: #ffdb4d;
}

.spendButton {
  background-color: #ff4d4d;
  color: white;
}

.spendButton:hover {
  background-color: #ff6666;
}

